<template>
  <v-container fluid>
    <v-data-iterator
      :items="items"
      :items-per-page.sync="itemsPerPage"
      :page="page"
      :search="search"
      :sort-by="sortBy.toLowerCase()"
      :sort-desc="sortDesc"
      hide-default-footer
    >
      <template v-slot:header>
        <v-toolbar dark class="mb-1">
          <v-text-field
            v-model="search"
            clearable
            flat
            outlined
            dense
            rounded
            hide-details
            prepend-inner-icon="mdi-magnify"
            label="Search"
          ></v-text-field>
          <template v-if="$vuetify.breakpoint.mdAndUp">
            <v-spacer></v-spacer>
            <v-btn-toggle v-model="sortDesc" mandatory>
              <v-btn small depressed color="green accent-4" :value="false">
                <v-icon>mdi-arrow-up</v-icon>
              </v-btn>
              <v-btn small depressed color="red" :value="true">
                <v-icon>mdi-arrow-down</v-icon>
              </v-btn>
            </v-btn-toggle>
          </template>
          <!-- dialog -->
          <v-divider vertical class="mx-4" inset></v-divider>
          <v-dialog max-width="500px" v-model="edited.dialog" persistent>
            <template v-slot:activator="{on}">
              <v-chip v-on="on" color="primary">
                <v-icon left>mdi-plus-circle</v-icon>Tambah Table
              </v-chip>
            </template>
            <v-card>
              <v-card-title>{{index > -1 ? 'Edit Projek' : 'Tambah Projek'}}</v-card-title>
              <v-card-text>
                <v-text-field v-model="edited.name"></v-text-field>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-chip color="red" @click="close">Close</v-chip>
                <v-divider vertical class="mx-4"></v-divider>
                <v-chip color="primary" @click="insert(edited, 'projects')">Save</v-chip>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>

      <template v-slot:default="props">
        <v-row>
          <v-col v-for="item in props.items" :key="item.id" cols="12" sm="6" md="3" lg="3">
            <v-card>
              <v-card-title class="subheading font-weight-bold">
                {{ item.name }}
                <v-spacer />
                <v-divider vertical class="mx-4"></v-divider>
                <v-tooltip right color="primary" rounded>
                  <template v-slot:activator="{on}">
                    <v-btn
                      fab
                      small
                      icon
                      color="primary"
                      @click="$router.push(`/relasi/${item.id}/${item.name}`)"
                    >
                      <v-icon v-on="on">mdi-link</v-icon>
                    </v-btn>
                  </template>
                  <span>Lihat Relasi</span>
                </v-tooltip>
              </v-card-title>
              <v-divider></v-divider>
              <v-list dense rounded>
                <v-list-item-group>
                  <v-list-item @click="$router.push(`/table/${item.id}/${item.name}`)">
                    <v-list-item-avatar>
                      <v-icon>mdi-table</v-icon>
                    </v-list-item-avatar>
                    <v-list-item-content>
                      <v-list-item-title>Jumlah Table</v-list-item-title>
                      <v-list-item-subtitle>{{item.child.length}}</v-list-item-subtitle>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
              <v-divider></v-divider>
              <v-card-actions>
                <v-chip color="red" pill @click="destroy(item, 'projects')">
                  <v-icon left>mdi-delete-outline</v-icon>Hapus
                </v-chip>
                <v-spacer></v-spacer>
                <v-chip color="primary" pill @click="edit(item)">
                  <v-icon left>mdi-database-edit</v-icon>Edit
                </v-chip>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </template>

      <template v-slot:footer>
        <v-row class="mt-2" align="center" justify="center">
          <span class="grey--text">Items per page</span>
          <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
              <v-btn dark text color="primary" class="ml-2" v-bind="attrs" v-on="on">
                {{ itemsPerPage }}
                <v-icon>mdi-chevron-down</v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item
                v-for="(number, index) in itemsPerPageArray"
                :key="index"
                @click="updateItemsPerPage(number)"
              >
                <v-list-item-title>{{ number }}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>

          <v-spacer></v-spacer>

          <span class="mr-4 grey--text">Page {{ page }} of {{ numberOfPages }}</span>
          <v-btn small fab dark color="blue darken-3" class="mr-1" @click="formerPage">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-btn small fab dark color="blue darken-3" class="ml-1" @click="nextPage">
            <v-icon>mdi-chevron-right</v-icon>
          </v-btn>
        </v-row>
      </template>
    </v-data-iterator>
  </v-container>
</template>

<script>
import axios from "axios";
var query = {
  delete: function(data) {
    return `delete from ${data.table} where id=${data.id}`;
  },
  insert: function(data, table) {
    return `insert into ${table} ('name') values ('${data.name}')`;
  },
  update: function(data, table) {
    return `update ${table} set ('name') = ('${data.name}') where id = ${data.id}`;
  }
};
export default {
  data() {
    return {
      itemsPerPageArray: [4, 8, 12],
      search: "",
      filter: {},
      sortDesc: false,
      page: 1,
      itemsPerPage: 4,
      sortBy: "name",
      dialog: false,
      snack: {
        model: false,
        text: null
      },
      edited: {},
      index: -1,
      keys: [
        "Name",
        "Calories",
        "Fat",
        "Carbs",
        "Protein",
        "Sodium",
        "Calcium",
        "Iron"
      ],
      items: []
    };
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.items.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter(key => key !== `Name`);
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      axios
        .get("?view=project&parent=projects&child=tables&key=project_id")
        .then(res => {
          this.items = res.data;
        });
    },
    destroy(item, table) {
      const c = confirm(`Hapus ${table} ?`);
      const token = "ShEAndIyLlu";
      if (c) {
        axios
          .get(`?exec=` + query.delete({ table: table, id: item.id }), {
            headers: {
              Authorization: `Bearer ${token}`
            }
          })
          .then(res => {
            this.snack = {
              model: true,
              text: res.data.message
            };
            this.getData(this.$route.params.id);
          });
      }
    },
    insert(item, table) {
      if (this.index > -1) {
        //Save new table
        axios
          .get(`?exec=` + query.update(item, table), {
            headers: {
              Authorization: `Bearer sdasjladn`
            }
          })
          .then(res => {
            this.snack = {
              model: true,
              text: res.data.message
            };
            this.getData(this.$route.params.id);
          });
      } else {
        //upadte table
        axios
          .get(`?exec=` + query.insert(item, table), {
            headers: {
              Authorization: `Bearer sdasjladn`
            }
          })
          .then(res => {
            this.snack = {
              model: true,
              text: res.data.message
            };
            this.getData(this.$route.params.id);
          });
      }
    },
    edit(item, type) {
      this.index = this.items.indexOf(item);
      this.edited = Object.assign({}, item);
      this.edited.dialog = true;
    },
    close() {
      this.edited.dialog = false;
      this.index = -1;
      this.edited = {};
    },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    }
  }
};
</script>