import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)
import VueCytoscape from 'vue-cytoscape'
Vue.use(VueCytoscape)

const routes = [{
        path: '',
        component: () =>
            import ('../components/dashboard.vue'),
        children: [{
                path: '/',
                name: 'Home',
                component: Home,
            },
            {
                path: '/table/:id/:name',
                name: 'Table',
                component: () =>
                    import ('../views/Table.vue'),
            },
            {
                path: '/parser',
                name: 'Parse Query',
                component: () =>
                    import ('../views/parser.vue'),
            },
        ],
    },
    {
        path: '/relasi/:id/:name',
        name: 'Relasi',
        component: () =>
            import ('../views/Relasi.vue'),
    },
    {
        path: '/about',
        name: 'About',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/About.vue'),
    },
]

const router = new VueRouter({
    routes,
})

export default router